
.NavigationBar {
  height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 5px;
  background-color: var(--grey);
  color: var(--gold);
}

.NavigationBar h1 {
  padding: 0;
  margin: 0;
}

.NavigationBar a {
  text-decoration: none;
  color: inherit;
}
