
.About {
  height: 100vh;
  width: 80vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}


.AboutRow {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}
.AboutRowRight {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  text-align: left;
}


.AboutBullet {
  margin: 15px;
}
